.App {
    position: relative;
}

h1 {
    background: #333;
    padding: 10px 0px; 
    width: 100%;
}

h1 a {
    color: white;
    text-decoration: none;
}

h1 a:hover {
    color: red;
}

.listing {
    width: 100%;
    background: #d5d5d5;
    position: absolute;
    z-index: 1;
}

.listing li {
    font-size: 20px;
    padding: 20px 0px;
    border-bottom: 1px solid black;
}

.listing li a {
    text-decoration: none;
    color: black; 
}

.listing li a:hover {
    color: blue;
}

/*overlay*/
.pageWrapper {
    position: fixed;
    text-align: left;
    /* margin: 0 auto; */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);  
    width: 80%;
    height: 80vh;
    padding: 10px;
    z-index: 99;
    box-sizing: border-box;
    background: white;
    overflow: scroll;
}

h2 {
    padding: 10px 10px;
    font-size: 30px;
    text-align: center;
    background: blue;
    color: #d5d5d5;
}

h3 {
    padding: 10px 0px;
    text-transform:capitalize; 
}

.comments {
    color: #333;
    padding: 10px;
    margin: 10px;
    border: 1px solid #300505;
    display: block;
}

.comments:hover {
    
}

.text {
    padding: 10px;
    display: block;
    border: 1px solid #333;
    border-left: 10px solid rgb(236, 119, 119);
}

.text:hover {
    border-left: 10px solid red;
}







